import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from '@angular/router';

import { AdminSideMenu, ActionLabel, Days, CommonExpands, EssentialRoute } from "@enum/app_enum";
import { YearService, ClassService } from "@core/services/serviceIndex";
import { AuthenticationService } from "@core/authentication/authentication.service";
import { Class } from '@app/shared/models/class';
import * as moment from 'moment'
import { forkJoin } from 'rxjs';

@Component({
  selector: 'app-class-create',
  templateUrl: './class-create.component.html',
  styleUrls: ['./class-create.component.css']
})
export class ClassCreateComponent implements OnInit {
  public routeDirLists: any = {
    routeName: [AdminSideMenu.Class, CommonExpands.Create],
    routeLink: [EssentialRoute.ClassList]
  };
  public submitted: boolean;
  public classForm: FormGroup;
  public yearList: any = [];
  public daysList: any = [];
  public days: any = [Days.Mon, Days.Tue, Days.Wed, Days.Thu, Days.Fri, Days.Sat, Days.Sun];
  public selectedDays: any = [];
  public selectedTeacher: any;
  public selectedStudents: any = [];
  public isTeacher: boolean = false;
  public isStudent: boolean = false;
  public classId: any;
  hoverStudentId:any;
  
  constructor(private formBuilder: FormBuilder,
    private _service: YearService, private _cService: ClassService,
    private _activeRoute: ActivatedRoute, private _router: Router,
    private _auth: AuthenticationService) { }

  ngOnInit(): void {
    this.classForm = this.formBuilder.group({
      name: ["", [Validators.required]],
      year: ["", [Validators.required]],
      startDate: ["", [Validators.required]],
      endDate: ["", [Validators.required]],
      startTime: ["", [Validators.required]],
      endTime: ["", [Validators.required]],
    });
    this.getClassId();
  }

  getClassId() {
    this._activeRoute.params.subscribe(params => {
      this.classId = params.id; // --> Name must match wanted parameter
      if (this.classId) {
        // this.routeDirLists = [AdminSideMenu.Class,CommonExpands.Manage,CommonExpands.Detail , ActionLabel.edit];
        let api1 = this._service.getYearList();
        let api2 = this._cService.classDetail(this.classId);
        this.routeDirLists.routeLink.push(EssentialRoute.ClassDetail + "/" + this.classId);
        this.routeDirLists.routeName = [AdminSideMenu.Class, CommonExpands.Detail, CommonExpands.Edit];
        forkJoin([api1, api2]).subscribe(results => {
          // results[0] is our api1
          // results[1] is our api2
          this.yearList = results[0].years;
          this.getClassDetail(results[1].class);
        });
      } else {
        this.getYearList();
      }


    });
  }

  getYearList() {
    this._service.getYearList()
      .subscribe((res) => {
        this.yearList = res.years;
      })
  }

  onSelectedDay(day: String) {
    if (this.selectedDays.includes(day)) {
      let index = this.selectedDays.findIndex((x: String) => day == x);
      this.selectedDays.splice(index, 1);
    } else this.selectedDays.push(day);
  }

  // convenience getter for easy access to form fields
  get f() { return this.classForm.controls; }

  onShowTeacherList() {
    this.isTeacher = true;

  }

  onSelectedTeacher(teacher: any) {
    this.selectedTeacher = teacher;
    this.isTeacher = false;
  }

  onCreate() {
    this.submitted = true
    if (this.classForm.invalid) return;
    else {
      let finalData = this.getFillData();
      this._cService.classCreate(finalData)
        .subscribe((res) => {
          console.log(res);
          this.goBack();
        })
    }
  }
  onEdit() {
    this.submitted = true
    if (this.classForm.invalid) return;
    else {
      let finalData = this.getFillData();
      this._cService.classUpdate(finalData)
        .subscribe((res) => {
          console.log(res);
          this.goBack();
        })
    }
  }
  getFillData() {
    let classData = new Class;
    if (this.classId)
      classData._id = this.classId;
    classData.name = this.classForm.value.name;
    classData.year = this.classForm.value.year;
    classData.startDate = this.changeDateToString(this.classForm.value.startDate);
    classData.endDate = this.changeDateToString(this.classForm.value.endDate);
    classData.startTime = this.getTime(this.classForm.value.startTime);
    classData.endTime = this.getTime(this.classForm.value.endTime);
    classData.teacher = this.selectedTeacher.userId;
    classData.students = this.getIdArray(this.selectedStudents);
    classData.days = this.selectedDays;
    return classData
  }
  getIdArray(arr: any) {
    return arr.map((obj: any) => {
      return obj.userId;
    });
  }
  changeDateToString(date: any) {
    return new Date(date).toString();
  }

  getTime(timeString: any) {
    var hourEnd = timeString.indexOf(":");
    var H = +timeString.substr(0, hourEnd);
    var h = H % 12 || 12;
    var ampm = (H < 12 || H === 24) ? " AM" : " PM";
    timeString = h + timeString.substr(hourEnd, 3) + ampm;
    return timeString;
  }


  onShowStudentList() {
    this.isStudent = true;
  }
  onSelectedStudent(studLists: any) {
    this.isStudent = false;
    this.selectedStudents = studLists;
  }

  getClassDetail(data: any) {
    this.classForm = this.formBuilder.group({
      name: [data.name, [Validators.required]],
      year: [data.year.yearId, [Validators.required]],
      startDate: [this.stringToDate(data.startDate), [Validators.required]],
      endDate: [this.stringToDate(data.endDate), [Validators.required]],
      startTime: [this.change24hours(data.startTime), [Validators.required]],
      endTime: [this.change24hours(data.endTime), [Validators.required]],
    });
    this.selectedTeacher = data.teacher;
    this.selectedDays = data.days;
    this.selectedStudents = data.students;
    
  }

  stringToDate(date: Date) {
    return moment(date).format('yyyy-MM-DD');
  }

  change24hours(time: Date) {
    return moment(time, ["h:mm A"]).format("HH:mm");
  }

  goBack() {
    let currentRoute = this._auth.currentUserValue.role.toLocaleLowerCase();
    this._router.navigateByUrl(currentRoute + '/' + EssentialRoute.ClassList);
  }




  onRemoveStudent(stud:any){
    let index = this.selectedStudents.findIndex((obj: any) => stud.userId == obj.userId);
    if(index==-1){
      this.selectedStudents.push(stud);
    }else{
      this.selectedStudents.splice(index, 1);
    }
  }
}
