import { Component, Input, OnInit } from '@angular/core';
import { ClassService } from "@core/services/class.service";

@Component({
  selector: 'app-class-feedback',
  templateUrl: './class-feedback.component.html',
  styleUrls: ['./class-feedback.component.css']
})
export class ClassFeedbackComponent implements OnInit {
  @Input() classId:any;

  constructor(private _cservice:ClassService) { }

  ngOnInit(): void {
    this.getClassFeedBack();
  }

  getClassFeedBack(){
    this._cservice.getClassFeedback(this.classId)
    .subscribe((res)=>{
      console.log(res);
    })
  }
}
