import { Component, OnInit,Output,EventEmitter, Input } from '@angular/core';
import { Role } from "@enum/app_enum";
import { UserService } from "@core/services/serviceIndex";

@Component({
  selector: 'app-student-selected',
  templateUrl: './student-selected.component.html',
  styleUrls: ['./student-selected.component.css']
})
export class StudentSelectedComponent implements OnInit {
  @Input() exitStud:any=[];
  @Output() onSelectedStudent: any = new EventEmitter<any>();

  public searchData: String;
  public limit: any = 10;
  public page: any = 1;
  public studentsList: any=[];
  public selectedStudents:any=[];
  public isShow:boolean=false;
  public selectedId:any=[];

  constructor(private _uService:UserService) { }

  ngOnInit(): void {
    this.selectedStudents=this.exitStud;
    this.selectedId=this.selectedStudents.map(stud=>{
      return stud.userId;
    })
    console.log(this.selectedId)
    this.getStudentList();
  }


  getStudentList() {
    this._uService.userLists(this.searchData, this.page, this.limit, "username", "asc", Role.Student)
      .subscribe(
        data => {
          this.studentsList = data.users;
          this.isShow=true;
        },
        error => {
          console.log(error);
          // this.loading = false;
        });
  }

  onsearch(val:String){
    this.searchData=val;
    this.getStudentList();
  }

  onSelectedStud(stud:any){
    console.log(stud)
    let index = this.selectedStudents.findIndex((obj: any) => stud.userId == obj.userId);
    if(index==-1){
      this.selectedStudents.push(stud);
      this.selectedId.push(stud.userId)
    }else{
      this.selectedStudents.splice(index, 1);
      this.selectedId.splice(index,1);
    }
  }

  onCheckIndex(data:any){
    return this.selectedStudents.findIndex((obj: any) => data.userId == obj.userId) == -1 ? false : true;
  }
  onConfirm(){
    this.onSelectedStudent.emit(this.selectedStudents);

  }

  close(){
    this.isShow=false;
    this.selectedStudents=[];
  }
}
