import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from "@angular/forms";

import { ListsComponent } from '@app/modules/class/lists/lists.component';
import { ClassCreateComponent } from '@app/modules/class/class-create/class-create.component';
import { ClassDetailComponent } from '@app/modules/class/class-detail/class-detail.component';
import { HomeWorkComponent } from '@app/modules/class/home-work/home-work.component';
import { ParentClassComponent } from '@app/modules/class/parent-class/parent-class.component';
import { TeacherSelectedComponent } from '@app/modules/class/teacher-selected/teacher-selected.component';
import { StudentSelectedComponent } from '@app/modules/class/student-selected/student-selected.component';
import { SelectedBlockComponent } from '@app/modules/class/selected-block/selected-block.component';
import { ClassBlockComponent } from '@app/modules/class/class-block/class-block.component';
// other
import { Role } from "@enum/app_enum";
import { AuthGuardService } from "@core/guards/auth-guard.service";
import { BasicComponentsModule } from "@basic-components/basic-components.module";
import { PipeModule } from '@pipes/pipe.module';
import { ClassFeedbackComponent } from './class-feedback/class-feedback.component';



const comLists = [
  ListsComponent,
  ClassCreateComponent,
  ClassDetailComponent,
  HomeWorkComponent,
  ParentClassComponent,
  ClassBlockComponent,
];

const routes: Routes = [
  {
    path: 'management',
    component: ListsComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin,Role.Teacher,Role.Parent,Role.StudentAffair,Role.Student] },
    
  },
  {
    path: 'management/detail/:id',
    component: ClassDetailComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin,Role.Teacher,Role.Parent,Role.StudentAffair,,Role.Student] }
  },
  {
    path: 'create',
    component: ClassCreateComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin,Role.StudentAffair] }
  },
  {
    path: 'management/detail/edit/:id',
    component: ClassCreateComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin,Role.StudentAffair] }
  },
  
];

@NgModule({
  declarations: [
    comLists,
    TeacherSelectedComponent,
    StudentSelectedComponent,
    SelectedBlockComponent,
    ClassFeedbackComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    BasicComponentsModule,
    ReactiveFormsModule,
    FormsModule,
    PipeModule
  ],
  exports: [
    comLists
  ],
})
export class ClassModule { }
