import { Component, Input, OnInit } from '@angular/core';
import { HomeworkService,ClassService } from "@core/services/serviceIndex";
import { AuthenticationService } from "@core/authentication/authentication.service";
import { Role } from "@enum/app_enum";

@Component({
  selector: 'app-home-work',
  templateUrl: './home-work.component.html',
  styleUrls: ['./home-work.component.css']
})
export class HomeWorkComponent implements OnInit {
  @Input() classId:String;
  @Input() teacherData:any;

  public isHWCreate:boolean=false;
  public hwIndex:any;

  public userole:any;
  public role:any=Role;
  public homework:any=[];
  public isLoading:boolean=false;
  public hwValue:any;
  public replyValue:any;
  public isImageLoad:boolean=true;

  constructor(private _service:HomeworkService,private _auth:AuthenticationService,private _classService:ClassService) { }

  ngOnInit(): void {
    this.isLoading=true;
    this.userole=this._auth.currentUserValue.role;
    console.log(this.userole)
    this.getHomeWork()
  }

  getHomeWork(){
    this._service.getHomework(this.classId).subscribe((res)=>{
      console.log(res);
      this.homework=res.homeworks;
      this.isLoading=false;
    })
  }

  createHomework(){
    this.isHWCreate=true;
  }

  gethwValue(val:String){

    this.hwValue=val;
   
  }

  createHw(){
    this.isLoading=true;
    this._classService.createHomework(this.classId,this.hwValue)
    .subscribe((res)=>{
      console.log(res);
      this.isHWCreate=false;
      this.getHomeWork();
    })
  }

  getHWId(id:String){
    console.log(id);
    if(this.hwIndex){
      this.hwIndex=null;
      this.replyValue=null;
    }

    else
    this.hwIndex=id;
  }

  getReplyValue(val:String){
    this.replyValue=val;
  }

  createReply(id:String){
    this.isLoading=true;
    this._classService.createReply(this.classId,id,this.replyValue)
    .subscribe((res)=>{
      console.log(res);
      this.hwValue=null;
      this.hwIndex=null;
      this.getHomeWork();
    })
  }

  onLoad(){
    this.isImageLoad=false;
    console.log(this.isImageLoad);
  }
}
