import { Component, OnInit, Output,EventEmitter } from '@angular/core';

import { Role } from "@enum/app_enum";
import { UserService } from "@core/services/serviceIndex";

@Component({
  selector: 'app-teacher-selected',
  templateUrl: './teacher-selected.component.html',
  styleUrls: ['./teacher-selected.component.css']
})
export class TeacherSelectedComponent implements OnInit {
  public searchData: String;
  public limit: any = 10;
  public page: any = 1;
  public teacherList: any;

  @Output() onSelectedTeacher: any = new EventEmitter<any>();

  constructor(private _uservice: UserService) { }

  ngOnInit(): void {
    this.getTeacherList();
  }

  getTeacherList() {
    this._uservice.userLists(this.searchData, this.page, this.limit, "username", "asc", Role.Teacher)
      .subscribe(
        data => {
          this.teacherList = data.users;
        },
        error => {
          console.log(error);
          // this.loading = false;
        });
  }

  onsearch(val: String) {
    this.searchData = val;
    this.getTeacherList();
  }

  onselectedTeacher(teacher:any){
    this.onSelectedTeacher.emit(teacher);
  }

}
