import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Role } from "@enum/app_enum";

// internal 
import { AuthenticationService } from "@core/authentication/authentication.service";
import { ClassService } from "@core/services/serviceIndex";

@Component({
  selector: 'app-lists',
  templateUrl: './lists.component.html',
  styleUrls: ['./lists.component.css']
})
export class ListsComponent implements OnInit {
  public routeDirLists: any={
    routeName:[],
    routeLink:[]
  };
  public currentUserRole: String;
  public classLists: any =[];
  public selectedRouteRole: String;
  public direction: String;
  public page:any=1;
  public search: String;
  public isclassEmpty:boolean=false;
  public role:any=Role;

  constructor(private _classService: ClassService,
    private _auth: AuthenticationService,
    private _router: Router,) { }

  ngOnInit(): void {
    this.currentUserRole = this._auth.currentUserValue.role;
    this.classLists = [];
    this.routeDirLists.routeName.push("Class");
    // this.routeDirLists.push(this._router.url.split("/")[this._router.url.split("/").length - 1]);
    this.getClassLists();
  }

  onsearch(val: String) {
    this.search = val;
    this.classLists=[];
    this.getClassLists();
  }

  getClassLists() {
    this._classService.classLists(this.search, this.direction,this.page).subscribe((res) => {
      if (this.direction) {
        this.isclassEmpty=res.classes.length==0;
        this.classLists = this.classLists.concat(res.classes);
      } 
      else if(this.search){
        this.classLists=res.classes;
        this.page=1;
      }
      else {
        this.classLists = res.classes;
      }
    })
  }

  onScroll(event: any) {
    var element = event.target;
    if (element.scrollHeight - element.scrollTop === element.clientHeight) {
      if(!this.isclassEmpty){
        this.direction = "down";
        this.page+=1;
        this.getClassLists()
      }
    }
  }
}
