import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ActionLabel, EssentialRoute, AdminSideMenu, CommonExpands, Role } from "@enum/app_enum";
import { ClassService } from "@core/services/serviceIndex";
import { AuthenticationService } from "@core/authentication/authentication.service";
@Component({
  selector: 'app-class-detail',
  templateUrl: './class-detail.component.html',
  styleUrls: ['./class-detail.component.css']
})
export class ClassDetailComponent implements OnInit {
  public routeDirLists: any = {
    routeName: [AdminSideMenu.Class, CommonExpands.Detail],
    routeLink: [EssentialRoute.ClassList]
  };
  public classEdit: any;
  public tabLists: any = [ActionLabel.view, ActionLabel.timetable, ActionLabel.homework, ActionLabel.feedback];
  public tabName = ActionLabel;
  public classId: String;
  public classDetail: any;
  public isLoading: boolean = false;
  public isDelete: boolean = false;
  public activeIndex: any = 0;
  public currentRole: any;
  public role: any = Role.Admin;

  constructor(private _activeRoute: ActivatedRoute,
    private _classService: ClassService, private _auth: AuthenticationService) { }

  ngOnInit(): void {
    this.isLoading = true;
    this._activeRoute.params.subscribe(params => {
      this.classId = params.id; // --> Name must match wanted parameter
      this.classEdit = "admin/" + EssentialRoute.ClassEdit + "/" + this.classId;
      this.currentRole = this._auth.currentUserValue.role;
    });
    this.getClassDetail();
  }

  tabChange(val: any) {
    this.activeIndex = val;
  }

  getClassDetail() {
    this._classService.classDetail(this.classId).subscribe((res) => {
      this.classDetail = res.class;
      this.isLoading = false;
    });
  }

  onDeleteModal() {
    this.isDelete = true;
  }

  onDeleteClass() {
    this._classService.deleteClass(this.classId)
      .subscribe(() => {
        this.isDelete = false;;
      });
  }
}
